<template>
  <div class="container">
      <router-link :to="{ name: 'Add' }"><button class="btn btn-add btn-secondary">Add New Person</button></router-link>
   <div class="clearfix"></div>
    <h2 class="page-title">People list</h2>
    <br />
    <h3>Search By</h3>
    <input class="form-control" type="text"  v-model="searchName" placeholder="Name.."/>
    <br />
    <input class="form-control" type="text"  v-model="searchAge" placeholder="Age.."/>
    <br />
    <input class="form-control" type="text"  v-model="searchCity" placeholder="City.."/>
    <br />
    <input class="form-control" type="text"  v-model="searchCompany" placeholder="Company.."/>
    <br />
    <input class="form-control" type="text"  v-model="searchPhone" placeholder="Phone.."/>
    <br />
    <input class="form-control" type="text"  v-model="searchEmail" placeholder="Email.."/>
    <br />
     <div class="form-group">
             <label class="lbl">Occupation </label>
             <select  @change="handleOcupationChange($event)" v-model="selectedOcupation" class="form-control" >
              <option v-for="(option, key) in occupations.options" v-bind:value="option.value" :key="key">
                {{option.text}}
              </option>
            </select>
    </div>
    <div class="row">
      <div class="col-md-12">
        <div class="table-responsive">
          <table class="table table-bordered table-striped table-hover">
            <thead>
              <tr>
                <th class="index">#</th>
                <th class="id">Id</th>
                <th class="name">Name</th>
                <th class="age">Age</th>
                <th class="occupation">Occupation</th>
                <th class="city">City</th>
                <th class="company">Company</th>
                <th class="phone">Phone</th>
                <th class="email">Email</th>
                <th class="delete"></th>
                <th class="edit"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(person, personIndex) in filtered || people" :key="personIndex" >
                <td class="index">{{ personIndex }}</td>
                <td class="id">{{ person.id }}</td>
                <td class="name">{{ person.name }}</td>
                <td class="age">{{ person.age }}</td>
                <td class="occupation">{{ person.occupation }}</td>
                <td class="city">{{ person.city }}</td>
                <td class="company">{{ person.company }}</td>
                <td class="phone">{{ person.phone }}</td>
                <td class="email">{{ person.email }}</td>
                <td class="action"><button type="button" class="btn btn-danger" v-on:click="remove(person.id)">DELETE</button></td>
                <td>
                   <router-link :to="{ name: 'Edit', params: { id: person.id } }"><button class="btn btn-primary">EDIT</button></router-link>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);
export default {
  name: "List",
  data() {
    return {
      searchName: "",
      searchAge: "",
      searchCity: "",
      searchCompany: "",
      searchPhone: "",
      searchEmail: "",
      selectedCity: "",
      selectedOcupation: ""
    };
  },
  computed: {
    people: {
      set: function() {
        this.people = this.filtered;
      },
      get: function() {
        return this.$store.state.people;
      }
    },
    cities: {
      get: function() {
        return this.$store.state.cities;
      },
      set: function() {}
    },
    occupations: {
      get: function() {
        return this.$store.state.occupations;
      },
      set: function() {}
    },
    filtered: {
     
      set: function(newValue) {
        console.log(newValue);
        this.$store.state.people = newValue
      },
      get: function() {
        //search by name
        var searched = this.$store.state.people;
        if (this.searchName) {
          searched = searched.filter(person => {
            return (
              person.name.toLowerCase().indexOf(this.searchName.toLowerCase()) >
              -1
            );
          });
        }

        //include search by age
        var searchedAge = parseInt(this.searchAge);
        if (typeof searchedAge === "number" && searchedAge === searchedAge) {
          searched = searched.filter(person => {
            return person.age === searchedAge;
          });
        }

        //search by city
        if (this.searchCity) {
          searched = searched.filter(person => {
            return (
              person.city.toLowerCase().indexOf(this.searchCity.toLowerCase()) >
              -1
            );
          });
        }

        //search by company
        if (this.searchCompany) {
          searched = searched.filter(person => {
            return (
              person.company
                .toLowerCase()
                .indexOf(this.searchCompany.toLowerCase()) > -1
            );
          });
        }

        //search by phone
        if (this.searchPhone) {
          searched = searched.filter(person => {
            return (
              person.phone
                .toLowerCase()
                .indexOf(this.searchPhone.toLowerCase()) > -1
            );
          });
        }

        //search by email
        if (this.searchEmail) {
          searched = searched.filter(person => {
            return (
              person.email
                .toLowerCase()
                .indexOf(this.searchEmail.toLowerCase()) > -1
            );
          });
        }

        return searched && searched.length ? searched : null;
      }
    }
  },
  created() {
    this.$store.dispatch("loadData"); // dispatch loading
  },
  methods: {
    remove(id) {
      var searched = this.people.findIndex(val => {
        return val.id == id;
      });

      confirm("Are you sure you want to delete this record?") === true
        ? this.people.splice(searched, 1)
        : void 0;
    },
    handleOcupationChange(e) {
      if (e.target.options.selectedIndex > -1) {
        var result = this.filtered.filter(person => {
          return (
            person.occupation.toLowerCase() ===
            this.selectedOcupation.toLowerCase()
          );
        });
        this.filtered = result;
        // console.log(result);
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped >
.lbl {
  float: left;
  font-weight: 600;
}
.btn-add {
  float: right;
  margin-bottom: 10px;
}
.clearfix {
  clear: both;
}
.name,
.phone {
  white-space: nowrap;
}
.page-title {
  color: #a1a1a1;
}
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>





